import "./App.css";
import React, { useEffect } from 'react';
import { BrowserRouter, Router, Switch } from 'react-router-dom';
import LandingPage from './components/LandingPage/LandingPage';
import Home from './components/Home/Home';
import Activities from './components/Activity/activities';
import CountryId from './components/Countries/CountryId';
import NotFound from './components/NotFound/NotFound';
import { useDispatch } from 'react-redux';
import { obtenerPaises } from './actions/index';



function App() {

const dispatch = useDispatch();

  useEffect(() => {
    dispatch(obtenerPaises())
  }, [dispatch]);


  return (
   <BrowserRouter>
     <div className="App">
        <Switch>
          <Router exact path='/' component={LandingPage} />
          <Router exact path='/home' component={Home} />
          <Router path='/activities' component={Activities} />
          <Router exact path='/countries/:id' component={CountryId} />
          <Router path='*' component={NotFound} />
        </Switch>
      </div>
   </BrowserRouter>
  );
}


export default App;
