import { useEffect } from "react";
import { useDispatch, useSelector} from 'react-redux';
import { Link, useParams } from 'react-router-dom';
import { obtenerDetalles } from '../../actions/index';
import Activity from './ActivityDetail';
import style from './countryId.module.css';

const CountryId = () => {
    const countryDetail = useSelector((state) => state.countryDetail);
    const dispatch = useDispatch();
    let { id } = useParams();

    useEffect(() => {
        dispatch(obtenerDetalles(id))
    }, [id]);

    return (
        <div className={style.all}>
            <button className={style.butn}>
                <Link className={style.link} to='/home'> ‚è™ Volver atr√°s </Link>
            </button>
        <div className={style.countryContainer}>
            <h1> {countryDetail.name} </h1>
            <h3> {countryDetail.id} </h3>
            <div className={style.imgContainer}>
                <img src={countryDetail.img} alt='No img' />
            </div>
            <h4>Continente: {countryDetail.continent} </h4>
            <h4>Capital: {countryDetail.capital} </h4>
            <h5>√Årea: {countryDetail.area} </h5>
            <h5>Poblaci√≥n: {countryDetail.population} habitantes </h5>
            <div className={style.activity}>
                <Activity countryName={countryDetail.name} activities={countryDetail.activities} /> 
            </div>
        </div>
        </div>
    )

};


export default CountryId;